import { KeyboardEvent, useRef, useState } from 'react';
import './style.css';
import InputBox from 'components/InputBox';
import { useCookies } from 'react-cookie';
import { useUserStore } from 'stores';
import { loginInfoMock } from 'mocks';
import { LoginUser } from 'types';
import { useNavigate } from 'react-router-dom';
import { MAIN_PATH } from 'constant';
import { Address, useDaumPostcodePopup } from 'react-daum-postcode';
import { signInRequest, signUpRequest } from 'apis';
import { SignInRequestDto, SignUpRequestDto } from 'apis/dto/request/auth';
import { SignInResponseDto } from 'apis/dto/response/auth';
import ResponseDto from 'apis/dto/response';

//          component:            //
export default function Authentication() {

  //          state:              //
  const { user, setUser } = useUserStore();
  //          state:            //
  const [cookies, setCookie] = useCookies();
  //          state:            //
  const [view, setView] = useState<'sign-in' | 'sign-up'>('sign-in');

  //          function:            //
  const navigator = useNavigate();

  //          component: sign in            //
  const SignInCard = () => {

    //          state:               //
    const passwordRef = useRef<HTMLInputElement | null>(null);

    //          state:             //
    const [email, setEmail] = useState<string>('');
    //          state:             //
    const [password, setPassword] = useState<string>('');
    //          state:              //
    const [passwordType, setPasswordType] = useState<'text' | 'password'>('password');
    //          state:               //
    const [passwordIcon, setPasswordIcon] = useState<'eye-off-icon' | 'eye-on-icon'>('eye-off-icon');
    //          state:             //
    const [error, setError] = useState<boolean>(false);

    //          function: sign in response            //
    const signInResponse = (responseBody: SignInResponseDto | ResponseDto) => {
      const { code } = responseBody;
      if (code === 'VF') alert(' .');
      if (code === 'SF') setError(true);
      if (code === 'DBE') alert(' .');
      if (code !== 'SU') return;

      const { token, expirationTime } = responseBody as SignInResponseDto;

      const now = new Date().getTime();
      const expires = new Date(now + expirationTime * 1000);

      setCookie('accessToken', token, { expires, path: MAIN_PATH });
      navigator(MAIN_PATH);

    }

    //          event handler:   key down            //
    const onEmailKeyDownHandler = (event: KeyboardEvent<HTMLInputElement>) => {
      if (event.key !== 'Enter') return;
      if (!passwordRef.current) return;
      passwordRef.current.focus();
    }

    //          event handler:   key down            //
    const onPasswordKeyDownHanlder = (event: KeyboardEvent<HTMLInputElement>) => {
      if (event.key !== 'Enter') return;
      onSignInButtonClickHandler();
    }
    //          event handler:                //
    const onPasswordIconClickHandler = () => {
      if (passwordType === 'text') {
        setPasswordType('password');
        setPasswordIcon('eye-off-icon');
      }
      if (passwordType === 'password') {
        setPasswordType('text');
        setPasswordIcon('eye-on-icon');
      }
    }

    //          event handler:               //
    const onSignInButtonClickHandler = () => {
      const requestBody: SignInRequestDto = {email, password};
      signInRequest(requestBody).then(signInResponse);
    }

    //          event handler:               //
    const onSignUpLinkClickHandler = () => {
      setView('sign-up');
    }

    //          render: sign in            //
    return (
      <div className='auth-card'>
        <div className='auth-card-top'>
          <div className='auth-card-title-box'>
            <div className='auth-card-title'>{''}</div>
          </div>
          <InputBox label=' ' type='text' placeholder='  .' error={error} value={email} setValue={setEmail} onKeyDown={onEmailKeyDownHandler} />
          <InputBox ref={passwordRef} label='' type={passwordType} placeholder=' .' error={error} value={password} setValue={setPassword} icon={passwordIcon} onKeyDown={onPasswordKeyDownHanlder} onButtonClick={onPasswordIconClickHandler} />
        </div>
        <div className='auth-card-bottom'>
          {error && (
          <div className='auth-sign-in-error-box'>
            <div className='auth-sign-in-error-message'>
              {'     .\n   .'}
            </div>
          </div>
          )}
          <div className='auth-button' onClick={onSignInButtonClickHandler}>{''}</div>
          <div className='auth-description-box'>
            <div className='auth-description'>{' ? '}<span className='description-emphasis' onClick={onSignUpLinkClickHandler}>{''}</span></div>
          </div>
        </div>
      </div>
    );
  }
  
  //          component: sign up            //
  const SignUpCard = () => {

    //          state:             //
    const [page, setPage] = useState<1 | 2>(1);

    //          state:            //
    const [email, setEmail] = useState<string>('');
    //          state:             //
    const [emailError, setEmailError] = useState<boolean>(false);
    //          state:              //
    const [emailErrorMessage, setEmailErrorMessage] = useState<string>('');

    //          state:            //
    const [password, setPassword] = useState<string>('');
    //          state:             //
    const [passwordType, setPasswordType] = useState<'text' | 'password'>('password');
    //          state:             //
    const [passwordIcon, setPasswordIcon] = useState<'eye-on-icon' | 'eye-off-icon'>('eye-off-icon');
    //          state:             //
    const [passwordError, setPasswordError] = useState<boolean>(false);
    //          state:              //
    const [passwordErrorMessage, setPasswordErrorMessage] = useState<string>('');

    //          state:             //
    const [passwordCheck, setPasswordCheck] = useState<string>('');
    //          state:              //
    const [passwordCheckType, setPasswordCheckType] = useState<'text' | 'password'>('password');
    //          state:              //
    const [passwordCheckIcon, setPasswordCheckIcon] = useState<'eye-on-icon' | 'eye-off-icon'>('eye-off-icon');
    //          state:              //
    const [passwordCheckError, setPasswordCheckError] = useState<boolean>(false);
    //          state:               //
    const [passwordCheckErrorMessage, setPasswordCheckErrorMessage] = useState<string>('');

    //          state:            //
    const [nickname, setNickname] = useState<string>('');
    //          state:             //
    const [nicknameError, setNicknameError] = useState<boolean>(false);
    //          state:              //
    const [nicknameErrorMessage, setNicknameErrorMessage] = useState<string>('');

    //          state:             //
    const [telNumber, setTelNumber] = useState<string>('');
    //          state:              //
    const [telNumberError, setTelNumberError] = useState<boolean>(false);
    //          state:               //
    const [telNumbeErrorMessage, setTelNumberErrorMessage] = useState<string>('');

    //          state:            //
    const [address, setAddress] = useState<string>('');
    //          state:             //
    const [addressError, setAddressError] = useState<boolean>(false);
    //          state:              //
    const [addressErrorMessage, setAddressErrorMessage] = useState<string>('');

    //          state:             //
    const [addressDetail, setAddressDetail] = useState<string>('');

    //          state:            //
    const [consent, setConsent] = useState<boolean>(false);
    //          state:             //
    const [consentError, setConsentError] = useState<boolean>(false);

    //          function:                //
    const open = useDaumPostcodePopup();
    //          function: sign up response            //
    const signUpResponse = (code: string) => {
      if (code === 'VF') alert(' .');
      if (code === 'DE') {
        setEmailError(true);
        setEmailErrorMessage('   .');
        setPage(1);
      }
      if (code === 'DN') {
        setNicknameError(true);
        setNicknameErrorMessage('  .');
      }
      if (code === 'DT') {
        setTelNumberError(true);
        setTelNumberErrorMessage('   .');
      }
      if (code === 'DBE') alert(' .');
      if (code !== 'SU') return;

      setEmail('');
      setPassword('');
      setNickname('');
      setTelNumber('');
      setAddress('');
      setAddressDetail('');
      setConsent(false);
      setPage(1);
      setView('sign-in');

    }

    //          event handler:               //
    const onPasswordIconClickHandler = () => {
      if (passwordType === 'password') {
        setPasswordType('text');
        setPasswordIcon('eye-on-icon');
      }
      if (passwordType === 'text') {
        setPasswordType('password');
        setPasswordIcon('eye-off-icon');
      }
    }
    //          event handler:                //
    const onPasswordCheckIconClickHandler = () => {
      if (passwordCheckType === 'text') {
        setPasswordCheckType('password');
        setPasswordCheckIcon('eye-off-icon');
      }
      if (passwordCheckType === 'password') {
        setPasswordCheckType('text');
        setPasswordCheckIcon('eye-on-icon');
      }
    }
    //          event handler:               //
    const onAddressIconClickHandler = () => {
      open({ onComplete });
    }
    //          event handler:                //
    const onComplete = (data: Address) => {
      const address = data.address;
      setAddress(address);
    }
    //          event handler:              //
    const onConsentCheckHandler = () => {
      setConsent(!consent);
    }

    //          event handler:                //
    const onNextStepButtonClickHandler = () => {

      setEmailError(false);
      setEmailErrorMessage('');
      setPasswordError(false);
      setPasswordErrorMessage('');
      setPasswordCheckError(false);
      setPasswordCheckErrorMessage('');
      
      // description:    //
      const emailPattern = /^[a-zA-Z0-9]*@([-.]?[a-zA-Z0-9])*\.[a-zA-Z]{2,4}$/;
      const checkedEmail = !emailPattern.test(email);
      if (checkedEmail) {
        setEmailError(true);
        setEmailErrorMessage('  .');
      }
      // description:    //
      const checkedPassword = password.trim().length < 8;
      if (checkedPassword) {
        setPasswordError(true);
        setPasswordErrorMessage(' 8  .');
      }
      // description:     //
      const checkedPasswordCheck = password !== passwordCheck;
      if (checkedPasswordCheck) {
        setPasswordCheckError(true);
        setPasswordCheckErrorMessage(' .');
      }

      if (checkedEmail || checkedPassword || checkedPasswordCheck) return;

      setPage(2);
    }
    //          event handler:               //
    const onSignUpButtonClickHandler = () => {

      setNicknameError(false);
      setNicknameErrorMessage('');
      setTelNumberError(false);
      setTelNumberErrorMessage('');
      setAddressError(false);
      setAddressErrorMessage('');
      setConsentError(false);

      // description:     //
      const checkedNickname = nickname.trim().length === 0;
      if (checkedNickname) {
        setNicknameError(true);
        setNicknameErrorMessage(' .');
      }
      // description:      //
      const telNumberPattern = /^[0-9]{10,12}$/;
      const checkedTelNumber = !telNumberPattern.test(telNumber);
      if (checkedTelNumber) {
        setTelNumberError(true);
        setTelNumberErrorMessage(' .');
      }
      // description:     //
      const checkedAddress = address.trim().length === 0;
      if (checkedAddress) {
        setAddressError(true);
        setAddressErrorMessage(' .');
      }
      // description:    //
      if (!consent) setConsentError(true);

      if (checkedNickname || checkedTelNumber || checkedAddress || !consent) return;

      const requestBody: SignUpRequestDto = {
        email,
        password,
        nickname,
        telNumber,
        address,
        addressDetail,
        agreedPersonal: consent
      };

      signUpRequest(requestBody).then(signUpResponse);
    }

    //          render: sign up            //
    return (
    <div className='auth-card'>
      <div className='auth-card-top'>
        <div className='auth-card-title-box'>
          <div className='auth-card-title'>{''}</div>
          <div className='auth-card-title-page'>{`${page}/2`}</div>
        </div>
        {page === 1 && (<>
        <InputBox label=' *' type='text' placeholder='  .' value={email} setValue={setEmail} error={emailError} errorMessage={emailErrorMessage} />
        <InputBox label='*' type={passwordType} placeholder=' .' value={password} setValue={setPassword} icon={passwordIcon} error={passwordError} errorMessage={passwordErrorMessage} onButtonClick={onPasswordIconClickHandler} />
        <InputBox label=' *' type={passwordCheckType} placeholder='  .' value={passwordCheck} setValue={setPasswordCheck} icon={passwordCheckIcon} error={passwordCheckError} errorMessage={passwordCheckErrorMessage} onButtonClick={onPasswordCheckIconClickHandler} />
        </>)}
        {page === 2 && (<>
        <InputBox label='*' type='text' placeholder=' .' value={nickname} setValue={setNickname} error={nicknameError} errorMessage={nicknameErrorMessage} />
        <InputBox label=' *' type='text' placeholder='  .' value={telNumber} setValue={setTelNumber} error={telNumberError} errorMessage={telNumbeErrorMessage} />
        <InputBox label='*' type='text' placeholder=' ' value={address} setValue={setAddress} icon='right-arrow-icon' error={addressError} errorMessage={addressErrorMessage} onButtonClick={onAddressIconClickHandler} />
        <InputBox label=' ' type='text' placeholder='  .' value={addressDetail} setValue={setAddressDetail} error={false} />
        </>)}
      </div>
      <div className='auth-card-bottom'>
        {page === 1 && (
        <div className='auth-button' onClick={onNextStepButtonClickHandler}>{' '}</div>
        )}
        {page === 2 && (<>
        <div className='auth-consent-box'>
          <div className='auth-check-box' onClick={onConsentCheckHandler}>
            {consent ? (<div className='check-round-fill-icon'></div>) : (<div className='check-ring-light-icon'></div>)}
          </div>
          <div className={consentError ? 'auth-consent-title-error' : 'auth-consent-title'}>{''}</div>
          <div className='auth-consent-link'>{'>'}</div>
        </div>
        <div className='auth-button' onClick={onSignUpButtonClickHandler}>{''}</div>
        </>)}
        <div className='auth-description-box'>
          <div className='auth-description'>{'  ? '}<span className='description-emphasis'>{''}</span></div>
        </div>
      </div>
    </div>
    );
  }
  
  //          render:            //
  return (
    <div id='auth-wrapper'> 
      <div className='auth-container'>
        <div className='auth-jumbotron-box'>
          <div className='auth-jumbotron-contents'>
            <div className='jumbotron-icon'></div>
            <div className='auth-jumbotron-text-box'>
              <div className='auth-jumbotron-text'>{'.'}</div>
              <div className='auth-jumbotron-text'>{'HOONS BOARD .'}</div>
            </div>
          </div>
        </div>
        { view === 'sign-in' && <SignInCard /> }
        { view === 'sign-up' && <SignUpCard /> }
      </div>
    </div>
  );
  
}
